<style lang="less">
    @import './logout.less';
</style>
<template>
    <div class="logout-main">
        <div class="tip">
            {{$t('page_logout.text')}}
        </div>
        <div class="btn">
            <Button type="primary" size="large" @click="logout">{{$t('page_logout.btn')}}</Button>
        </div>
    </div>
</template>
<script>
import wallet from '../../libs/wallet/wallet';
    export default {
        name:'logout',
        data() {
            return {
                
            };
        },
        methods: {
            init(){

            },
            logout(){
                wallet.removeMnemonic();
                wallet.removeSecret();
                window.location.reload();
            }
            
        },
        mounted() {
            this.init();

        }
    };
</script>


